.features {
    color: #ffffff;

    min-height: 350px;
    position: relative;

    background-color: #222222;

    &::before {
        content: "";

        position: absolute;
        z-index: 2;
        width: 0;
        height: 0;

        border-bottom: 35px solid #222222;
        border-right: 50vw solid transparent;

        top: -34.5px;  
        left: 0;  

    }

    &::after {
        content: "";

        position: absolute; 
        z-index: 2;
        width: 0;
        height: 0;

        border-bottom: 35px solid #222222;
        border-left: 50vw solid transparent;

        top: -34.5px;         
        right: 0;

    }
}


.features__button {
    width: 100%;
    height: 13px;

    position: absolute;
    top: -40px;
    text-align: center;
    z-index: 5;
}

.features__button-link {
    display: block;
    width: 100%;  
    
    cursor: pointer;
}

.features__button-link svg {
    width: 26px;
    height: 13px;
}

.features-list {
    list-style: none;
    margin: 0;
    padding: 0 5% 60px;
}

.features-list__item {
    font-size: 20px;
    line-height: 1.35;
    font-weight: 300;
    text-align: center;
    padding: 30px 0 35px;

    
    position: relative;

    &::after {
        content: '';

        position: absolute;
        bottom: 0;
        left: 0;

        width: 100%;
        height: 1px;
        background-color: #eee;
        opacity: 0.2;
    }
}
